<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'设备维修项目'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-repair-project
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-repair-project>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditRepairProject from "./create-or-edit-repair-project/create-or-edit-repair-project.vue";
import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "DataV2RepairProject",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditRepairProject,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  mounted() {},
  created() {
    this.getBtnList();
  },
  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyNdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: "确认删除该维修项目吗？删除后无法恢复。",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 示例数据
        let dataList = [];
        const deviceTypes = [
          "交换机", 
          "路由器", 
          "服务器", 
          "存储设备", 
          "防火墙"
        ];
        
        const repairLevels = ["一级", "二级", "三级"];
        const statuses = ["启用", "禁用"];
        
        for (let i = 0; i < 25; i++) {
          const deviceIndex = i % 5;
          const levelIndex = i % 3;
          const projectTypes = ["故障排除", "部件更换", "性能优化", "固件升级", "硬件维修"];
          
          dataList[i] = {
            id: i + 1,
            projectCode: `RP${String(i + 1).padStart(3, '0')}`,
            name: `${deviceTypes[deviceIndex]}${projectTypes[i % 5]}项目`,
            applicableDevices: deviceTypes[deviceIndex],
            repairLevel: repairLevels[levelIndex],
            averageDuration: `${1 + i % 5}.${i % 6}小时`,
            requiredParts: i % 3 === 0 ? "无" : `${deviceTypes[deviceIndex]}标准配件${i % 4 + 1}套`,
            repairSteps: `包含${3 + i % 4}个步骤，需要专业技术人员操作`,
            status: statuses[i % 2],
            createdBy: "admin",
            createdTime: "2024-03-18 " + (10 + i % 10) + ":" + (i % 60),
          };
        }

        // filter 过滤
        if (this.filterText) {
          console.log(this.filterText);
          dataList = dataList.filter((item) => {
            return item.projectCode.indexOf(this.filterText) > -1 || 
                   item.name.indexOf(this.filterText) > -1 ||
                   item.applicableDevices.indexOf(this.filterText) > -1 ||
                   item.repairLevel.indexOf(this.filterText) > -1 ||
                   item.averageDuration.indexOf(this.filterText) > -1 ||
                   item.requiredParts.indexOf(this.filterText) > -1 ||
                   item.repairSteps.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style> 